.fact_container {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }
  
  .fact_container .fact_item {
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
  }
  
  .fact_container .count_container {
    display: flex;
    align-items: center;
  }
  
  .fact_container .indicator {
    font-size: 60px;
    font-weight: 900;
  }
  .fact_container .name {
    color: var(--color-off-white);
    font-size: 14px;
    text-align: center;
  }
  
  @media (max-width: 1100px) {
    .fact_container .indicator {
      font-size: 32px;
    }
    
    .fact_container .name {
      font-size: 12px;
    }
  }
  
  @media (max-width: 500px) {
    .fact_container .indicator {
      font-size: 20px;
    }
    
    .fact_container .name {
      font-size: 10px;
    }
  }
    